.free-grid {
  border: 0;
    border: 1px solid rgb(230, 230, 230);
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    overflow: hidden;
    display: block;
    position: relative;


  }


  .free-grid .free-grid-row {
    border: 0;
    width: 100%;
    min-width: 100%; /*without this left scrolltop will not be set when hidden*/
    border-bottom: 1px solid rgb(230, 230, 230);
    box-sizing: border-box;
    position: absolute;
  }


  .free-grid .free-grid-col {
    border: 0;
    border-right: 1px solid rgb(230, 230, 230);
    box-sizing: border-box;
    position: absolute;
    height: 100%;
  }

  .free-grid .free-grid-panel {
    border: 0;
    position: absolute;
    box-sizing: border-box;
    width: 100%;
    top: 0;
    border-bottom: 1px solid rgb(230, 230, 230);
    background-color: rgb(240, 240, 240);
  }
  
  .free-grid .free-grid-footer {
    border: 0;
    position: absolute;
    box-sizing: border-box;
    width: 100%;
    bottom: 0;
    border-top: 1px solid rgb(230, 230, 230);
    background-color: rgb(240, 240, 240);
  }
  
  .free-grid .free-grid-header {
    position: absolute;
    box-sizing: border-box;
    display: inline-block;
    min-width: 100%;
    background-color: rgb(240, 240, 240);
  }

  .free-grid .free-grid-body {
    position: absolute;
    box-sizing: border-box;
    border-top: 1px solid rgb(206, 202, 202);
    min-width: 100%;
    overflow-y: auto;
  }

  .free-grid .free-grid-content {
    background-color: white;
    min-width: 100%;
    z-index:6;
    position: absolute;
    box-sizing: border-box;
    height: 100%;
    overflow: hidden;
    overflow-x: hidden;
    overflow-y: hidden;
  }


  .free-grid .free-grid-selected-row {
    box-shadow: none;
    background-color: rgb(203, 195, 203);
  }



  .free-grid .free-grid-header-input-top {
    box-sizing: border-box;
    border: 0;
    border-bottom: 1px solid rgb(230, 230, 230);
    border-radius: initial;
    box-shadow: initial;
    height: 50%;
    width: 100%;
    background-color: white;
    padding: 5px 10px;
    margin: initial;
    transition: initial;
  }

  
  .free-grid .free-grid-header-input-bottom {
    box-sizing: border-box;
    border: 0;
    border-radius: initial;
    box-shadow: initial;
    border-top: 1px solid rgb(230, 230, 230);
    height: 50%;
    width: 100%;
    background-color: white;
    padding: 5px 10px;
    margin: initial;
    transition: initial;
  }


  .free-grid .free-grid-row-checkbox-100 {
    height: 100%;
    width: initial;
    left: initial;
    position: initial;
    display: block;
    opacity: initial;
    margin: auto;
  }
  
  .free-grid .free-grid-row-checkbox-50 {
    height: 50%;
    width: initial;
    opacity: initial;
    left: initial;
    position: initial;
    display: block;
    margin: auto;
  }


  .free-grid .free-grid-row-input {
    box-sizing: border-box;
    border: 0;
    border-radius: initial;
    box-shadow: initial;
    height: 100%;
    width: 100%;
    background-color: transparent;
    padding: 5px 10px;
  }
  
  .free-grid .free-grid-image-round {
    border-radius: 50%;
    height: 100%;
    box-sizing: border-box;
    position: relative;
    left: 50%;
    transform: translateX(-50%);
    padding-top: 5px;
  }
  
  .free-grid .free-grid-label-bottom {
    box-sizing: border-box;
    border: 0;
    border-radius: initial;
    box-shadow: initial;
    height: 50%;
    width: 100%;
    position: relative;
    text-align: center;
    overflow: hidden;
    user-select: none;
    margin: initial;
  }
  
  .free-grid .free-grid-label-top {
    box-sizing: border-box;
    border: 0;
    border-radius: initial;
    box-shadow: initial;
    height: 50%;
    width: 100%;
    position: relative;
    text-align: center;
    overflow: hidden;
    user-select: none;
    margin: initial;
  }
  
  /*.avg-default .avg-label-top p {
    margin: 0;
  }
  .avg-default .avg-label-bottom p {
    margin: 0;
  }*/
  
  .free-grid .free-grid-label-full {
    box-sizing: border-box;
    border: 0;
    border-radius: initial;
    box-shadow: initial;
    height: 100%;
    width: 100%;
    position: relative;
    text-align: center;
    user-select: none;
    margin: initial;
  }


  .free-grid .free-grid-draggable-handler {
    position: absolute;
    cursor: w-resize;
    width: 15px;
    right: 0;
    top: 0;
    bottom: 0;
    z-index: 900;
    border:initial;
  }
  
  .free-grid .free-grid-vGridDragHandle {
    cursor: move;
  }
  
  .free-grid .free-grid-drag {
    border: 1px solid rgb(230, 230, 230);
    box-sizing: border-box;
    box-shadow: initial;
    line-height: 50%;
    pointer-events: none;
    background-color: rgb(240, 240, 240);
    height: 25px;
    min-width: 100px;
    position: absolute;
    padding-top: 5px;
    text-align: center;
    z-index: 50;
  }


  .free-grid .free-grid-fa-sort-number[data-vgridsort]:after {
    font: x-small;
    font-size: 8px;
    content: attr(data-vgridsort);
  }
  
  
  .free-grid .free-grid-icon {
    fill: currentColor;
    width: 1em;
    height: 1em;
    vertical-align: -0.15em;
    overflow: hidden;
    display:initial;
  }
  
  .free-grid  .iconhidden{
    display:none
  }
  
 